*{
  font-family: 'Lato', sans-serif !important;
  font-weight: 300;
}
body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.landing {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.landing-image {
  width: 100vw;
  height: 100vh;
  position: absolute;
  background-image: url("https://images.unsplash.com/photo-1449247709967-d4461a6a6103?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb");
  /* background-image: url("https://cdn.vox-cdn.com/thumbor/njH3Nvv8FJeKnTzA0BkS-SjMT34=/0x0:5597x3731/1200x800/filters:focal(2352x1419:3246x2313)/cdn.vox-cdn.com/uploads/chorus_image/image/59372219/IMG_9635.0.jpg"); */
  /* background-image: url("https://images.squarespace-cdn.com/content/v1/59a8b2229f8dceb86590396c/1554321482434-MJ7ZG46YJMUCP4QUOPF5/ke17ZwdGBToddI8pDm48kCX-V5vw-8h9IBXN10-_8XN7gQa3H78H3Y0txjaiv_0fDoOvxcdMmMKkDsyUqMSsMWxHk725yiiHCCLfrh8O1z4YTzHvnKhyp6Da-NYroOW3ZGjoBKy3azqku80C789l0p4Wyba38KfG317vYluk45_zZdtnDCZTLKcP2mivxmYi50xvY5saIGKMgOza9mH4XA/thayne+robstad+-+Hearth-80.jpg?format=2500w"); */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: calc(50%);
  /* filter: blur(0.1vw);
  -webkit-filter: blur(0.1vw); */
  z-index: -1;
}

#UIbutton {
  width: 40vw;
  height: 7vw;
  border-radius: 10px;
  padding: 0 1em;
  color: white;
  border-color: white;
  background-color: rgba(0,0, 0, 0.25);
  /* font-size: 1.2em; */
}
#UIbutton h1{
  display: flex;
  justify-content: center;
  align-items: center;

}

#UIbutton:hover {
  background-color: rgba(0,0, 0, 0.5);
}

.Link {
  text-decoration: none !important;
  color: black;
}

.card {
  display: flex;
  align-items: center;
  width: 30vw;
}

.dashboard {
  width: 100vw;
  height: 100vh;
  display: inline-block;
}

.dashboard-image {
  background-color: #F5F5F5;
  width: 100vw;
  height: 100vh;
  position: absolute;
  /* background-image: url("https://img.freepik.com/free-vector/abstract-paper-hexagon-white-background_51543-213.jpg?size=626&ext=jpg");
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(0.5vw);
  -webkit-filter: blur(0.5vw); */
  z-index: -1;
}

.carousel {
  padding-bottom: '30px';
  position: 'relative';
}

.react-multi-carousel-item {
  width: 15vw;
  height: 15vw;
}

.carouselList {
  margin: 0 15px;
  overflow: hidden;
  height: 100%;
  background-color: white;
  border-radius: 15px;
}
.listContent {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  font-family: 'Lato';
  font-weight: 200;
  font-size: 1.5em;
  text-transform: uppercase;
}
.listContent:hover {
  background-color: rgba(100,100, 100, 0.15);
}
#paypperiodMonth{
  margin: 5px;
  min-width: 120px;
}
#monthSelect{
  width: 25vw;
}

.h1 {
  margin: 0 !important;
}
/* .MuiFormControl-root{
  margin: 0em 0.5em 1.5em 0.5em !important;
} */
.basic-info{
  display: grid;
  margin-left: 20vw;
  margin-right: 20vw;
  grid-template-columns: auto auto;
  align-content: center;
  justify-content: center;
  grid-column-gap: 5vw
}
.basic-info div {
  width: 100%;
  margin: 0em 0.5em 1em 0.5em;
}
#outlined-multiline-flexible-label{
  margin-left: 5px;
}
.info-name{
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row: 1;
}